<template>
    <div>
        <button class="button" @click="open1"></button>
        <button class="button" @click="open2">render</button>
    </div>
</template>
<script>

export default {
    methods: {
        open1(){
            this.$MyDialog.open({
                // 
                content: '<span class="dialog-content">{{message}}</span>',
                // 
                propsData: {
                    message: ''
                }
            })
        },
        open2(){
            this.$MyDialog.open({
                // renderthispropsData
                content: function(h){
                    return h('span', { class: 'dialog-content'}, [this.message])
                },
                // 
                propsData: {
                    message: 'render'
                }
            })
        },
    },
}
</script>



