<template>
    <button class="button" @click="open"></button>
</template>
<script>

let component = {
    data() {
        return {
            returnData: ''
        }
    },
    template: 
        `<div class="dialog-content">
            <input v-model="returnData" placeholder="test"/>
            <button class="button" 
                @click="$myDialog.close(returnData)">
                
            </button>
        </div>`
}

export default {
    methods: {
        open(){
            this.$MyDialog.open({
                content: component,
                title: 'test',
                onBeforeClose: (retrunData)=>{
                    // onBeforeClose
                    if(retrunData !== 'test'){
                        alert('test')
                        // false
                        return false
                    }
                },
            })
        }
    }
}
</script>



