`import Vue form 'vue'
import Vuex from 'vue'

Vue.use(Vuex)

let store = new Vue.Stroe({
    state: {
        count: 0
    }
})

new Vue({
    el: ...,
    store,
    template: '<button class="button" @click="open"></button>',
    methods: {
        open(){
            this.$MyDialog.open({
                // store
                content: \`<div class="dialog-content">
                            {{$store.state.count}}
                            <button class="button" @click="$store.state.count = $store.state.count + 1">
                                +1
                            </button>
                        </div>\`,
            })
        }
    }
}}

