<template>
    <Doc>
        <H1>my-vue-dialog</H1>
        <P><Strong>MyDailog</Strong><A href="https://cn.vuejs.org">Vue</A></P>
        <H2></H2>
        <Li></Li>
        <Li></Li>
        <Li></Li>
        <Li></Li>

        <H2></H2>
        <H3>Vue</H3>
        <P><Strong>MyDailog</Strong><Strong>2.5.0Vue</Strong></P>
