<template>
    <button class="button" @click="open"></button>
</template>
<script>

let component = {
    data: function(){
        return {
        }
    },
    template: 
        `<div class="dialog-content">
            <button class="button" @click="$myDialog.resize({width: 500, height: 400})">resize()</button>
            <button class="button" @click="$myDialog.setWidth(300)">setWidth()</button>
            <button class="button" @click="$myDialog.setHeight(200)">setHeight()</button>
        </div>`
}

export default {
    methods: {
        open(){
            this.$MyDialog.open({
                title: 'dialog',
                content: component       
            })
        }
    }
}
</script>



