<template>
    <button class="button" @click="open"></button>
</template>
<script>

let component = {
    // $myDialog.close()
    template: 
        `<div class="dialog-content">
            <p></p>
            <button class="button" @click="$myDialog.close()"></button>
        </div>`
}

export default {
    methods: {
        open(){
            this.$MyDialog.open({
                content: component       
            })
        }
    }
}
</script>



