<template>
    <button class="button" @click="open"></button>
</template>
<script>

let component = {
    props: {
        message: {
            type: String
        }
    },
    template: '<div class="dialog-content">{{message}}</div>'
}

export default {
    methods: {
        open(){
            this.$MyDialog.open({
                content: component,
                // propsDatacomponentmessageprop
                propsData: {
                    message: 'propsprop'
                }    
            })
        }
    }
}
</script>



