<template>
    <button class="button" @click="open"></button>
</template>
<script>

export default {
    data(){
        return {
            message: 'render'
        }
    },
    methods: {
        open(){
            this.$MyDialog.open({
                // renderthis
                content: (h)=>{
                    return h('span', { class: 'dialog-content'}, [this.message])
                },
            })
        },
    },
}
</script>



